import { InjectionToken, Provider } from '@angular/core';
import { AccessControlConfigProvider } from '@backbase/payment-orders-ang';

import { currencyFallback } from './models/data';

// These tokens can be used to change configuration at app level
export const BB_STOP_CHECK_ACCESS_CONTROL_CONFIG_TOKEN: InjectionToken<AccessControlConfigProvider> =
  new InjectionToken('BB_STOP_CHECK_ACCESS_CONTROL_CONFIG_TOKEN');

// These tokens can be used to change configuration at app level
export const BB_STOP_CHECK_CURRENCY_FALLBACK_TOKEN: InjectionToken<string> =
  new InjectionToken('BB_STOP_CHECK_CURRENCY_FALLBACK_TOKEN');

/**
 * Access control config values
 */
export const accessControlConfigDefaults: Partial<AccessControlConfigProvider> =
  {
    resourceName: 'Payments',
    businessFunction: 'Stop Checks',
    privilege: 'create',
  };

/**
 * Stop checks providers types
 */
export const StopCheckConfigProvider: Array<Provider> = [
  {
    provide: BB_STOP_CHECK_ACCESS_CONTROL_CONFIG_TOKEN,
    useValue: accessControlConfigDefaults,
  },
  {
    provide: BB_STOP_CHECK_CURRENCY_FALLBACK_TOKEN,
    useValue: currencyFallback,
  },
];
